/* SECTION TITLE */
h5 {
  margin-bottom: 16px;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #9b9b9b;
}

/* ===== PRICES BLOCK ===== */
.prices {
  background-color: #fafafa;
  border-radius: 12px;
  padding: 24px;

  &__container {
    width: 100%;
  }
}

/* GRID */
.p-grid {
  display: grid;
  grid-template-columns: 3fr 1.5fr 1.5fr auto;
  background-color: #ffffff;
  border-radius: 12px;
  overflow: hidden;
}

/* COMMON COLUMN STYLES */
.service__column,
.price__column,
.deadline__column,
.order__column {
  padding: 16px 20px;
}

.service__column,
.price__column,
.deadline__column {
  border-right: 1px solid #f0f0f0;
}

/* HEADER ROW */
.p__header {
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 500;
  color: #9b9b9b;
}

/* ROW TEXT */
.serv__text,
.price__text,
.deadline__text {
  padding: 14px 0;
  font-size: 14px;
  color: #222;
  border-top: 1px solid #f2f2f2;
}

.serv__text:first-of-type,
.price__text:first-of-type,
.deadline__text:first-of-type {
  border-top: none;
}

/* ORDER COLUMN */
.order__column {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

/* ORDER BUTTON */
.order__btn {
  align-self: flex-end;
  height: 32px;
  padding: 8px 16px;
  margin: 0;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #e64b77;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;

  &:first-of-type {
    margin-top: 68px;
  }

  &:hover {
    background-color: #d63f69;
  }
}

/* VAT TEXT */
.vat__text {
  margin-top: 16px;
  max-width: 720px;
  font-size: 12px;
  line-height: 1.5;
  color: #9b9b9b;
}

/* COMMERCIAL PROPOSAL BUTTON */
.prices > button {
  margin-top: 12px;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: none;
  border: none;
  font-size: 14px;
  font-weight: 500;
  color: #e64b77;
  cursor: pointer;

  &:hover {
    text-decoration: underline;
  }
}

/* FOOTER */
footer {
  margin-top: 40px;
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #f0f0f0;

  .footer {
    font-size: 12px;
    color: #9b9b9b;
    max-width: 300px;
  }
}

/* =========================
   PRICES: MOBILE SWIPER
   ========================= */

/* Hide mobile swiper by default (desktop/tablet) */
.prices__swiper {
  display: none;
}

/* Mobile: show swiper, hide table grid */
@media (max-width: 767px) {
  .price-container {
    display: none;
  }

  .prices {
    padding: 16px;
  }

  .prices__swiper {
    display: block;
  }

  .prices__swiper .swiper-wrapper {
    display: flex;
  }

  .prices__swiper .swiper-slide {
    width: 260px;
    flex-shrink: 0;
  }

  .prices-card {
    background: #fff;
    border-radius: 12px;
    padding: 16px;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.05);
    min-height: 200px;
  }

  .prices-card__label {
    margin: 0 0 4px;
    font-size: 12px;
    color: #9b9b9b;
  }

  .prices-card__value {
    margin: 0 0 12px;
    font-size: 14px;
    color: #222;
  }

  .prices-card__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 12px;
    margin-top: 4px;
  }

  .prices-card__term {
    font-size: 14px;
    color: #222;
  }

  .prices-card__order {
    height: 32px;
    padding: 0 16px;
    border: none;
    border-radius: 20px;
    background: #e64b77;
    color: #fff;
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    cursor: pointer;
  }

  .prices__swiper .swiper-pagination {
    margin-top: 16px;
    position: relative;
    text-align: center;
  }
}
